import React, {useState} from 'react'
import GifOne from './OneGi';
import PostComments from './PostComments';
import Comments from './Comments';

const OneGif = () => {
  const [comment, setComment] = useState('');
  const handleSubmit = e => {
    e.preventDefault();
    const data = {
      comment: comment
    }
    console.log(data);
  };
  const handleChange = e => {
    setComment(e.target.value);
  };
  return (
    <div className="one-art-cont">
      <div className="One-Article">
        <div className="one-art-header row">
          <img src={GifOne.user.imgUrl} alt="Profile-pix" />
          <h6>{GifOne.user.name}</h6>
          <span>{GifOne.timestamp.toString()}</span>
        </div>
        <div className="one-art-body">
          <h6>{GifOne.gif_title}</h6>
          <div>
            <img src={GifOne.gif_Url} alt="Gifs" />
          </div>
        </div>
      </div>
      <PostComments
        handleSubmit={handleSubmit}
        handleChange={handleChange}
        value={comment}
      />
      <h6 style={{color: 'white', textAlign: 'center'}}>Comments</h6>
      <div className="comment-container">
        <ul>
          {GifOne.comments.map(comment => (
            <Comments
              key={comment.id}
              username={comment.user.name}
              timestamp={comment.timestamp}
              comments={comment.commentBody}
            />
          ))}
        </ul>
      </div>
    </div>
  )
}

export default OneGif
