import React, { useState } from 'react';
import OneArt from './OneArt';
import PostComments from './PostComments';
import Comments from './Comments';

const OneArticle = () => {
  const [comment, setComment] = useState('');
  const handleSubmit = e => {
    e.preventDefault();
    const data = {
      comment: comment
    }
    console.log(data);
  };
  const handleChange = e => {
    setComment(e.target.value);
  };
  return (
    <div className="one-art-cont">
      <div className="One-Article">
        <div className="one-art-header row">
          <img src={OneArt.user.imgUrl} alt="Profile-pix" />
          <h6>{OneArt.user.name}</h6>
          <span>{OneArt.timestamp.toString()}</span>
        </div>
        <div className="one-art-body">
          <div>
            <h6>{OneArt.article_title}</h6>
            {OneArt.article_article}
          </div>
        </div>
      </div>
      <PostComments
        handleSubmit={handleSubmit}
        handleChange={handleChange}
        value={comment}
      />
      <h6 style={{color: 'white', textAlign: 'center'}}>Comments</h6>
      <div className="comment-container">
        <ul>
          {OneArt.comments.map(comment => (
            <Comments
              key={comment.id}
              username={comment.user.name}
              timestamp={comment.timestamp}
              comments={comment.commentBody}
            />
          ))}
        </ul>
      </div>
    </div>
  );
};

export default OneArticle;
