import React, {useState} from 'react'
import {NavLink} from 'react-router-dom'
import datas from '../../containers/mock.json'
import Article from './Article.js';
import Gif from './Gif.js';
import User from './user.js';
import TopStories from './TopStories.js';

const Timeline = () => {
    const [data] = useState(datas);
    const [date] = useState(
      new Date().toLocaleString()
    );
    const abbe = data.map((post) => {
      return(
        <li key={post.id}>
          {
            post.type === 'gif' ? 
            (
            <Gif 
            user_img={post.user.imgUrl} 
            name={post.user.name} 
            gif_title={post.gif_title} 
            gif_Url={post.gif_Url}
            date={date}
            />
            ) : (
            <Article  
            user_img={post.user.imgUrl} 
            name={post.user.name} 
            article_title={post.article_title} 
            article_article={post.article_article}
            date={date}
            />
            )
          }
        </li>
      )
    })
  return (
    <div className="row ">
      <User name="John Doe" user_url="https://res.cloudinary.com/kayode/image/upload/v1573225061/avatar-profile_wg0sms.svg" />
      <div className="col-sm-6">  
        <div className="post-container"> 
      <div className="post-btn">
          <NavLink to="/post"><button className="write-article">Write Article</button></NavLink>
          <NavLink to="/gif"><button className="post-gif">Post Gifs</button></NavLink>
      </div>
      <ul className="feed-list">
        {abbe}
      </ul>
      </div>
      </div>
      <TopStories />
    </div>
  )
}

export default Timeline
