import React, { useState } from 'react';
import Tag from './Tag';

const CreateGif = () => {
  const [tag, setTag] = useState([]);
  const [inputs, setInputs] = useState({
    title: '',
    tags: [],
    file: '',
    type: 'gif'
  })

  const handleTagChange = e => {
    setTag(e.target.value);
  };
  const handleChange = e => {
    const { name, value } = e.target;
    setInputs({ ...inputs, [name]: value });
  }
  const handleClick = e => {
    setInputs({ ...inputs, tags: inputs.tags.concat(tag) });
    setTag('');
  }
  const handleImage = e => {
    setInputs({...inputs, file: e.target.files[0]})
  }
  const handleSubmit = e => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('title', inputs.title);
    formData.append('imgUrl', inputs.file);
    formData.append('tags', inputs.tags);
    formData.append('type', inputs.type);
    for(let value of formData.values()) {
      console.log(value)
    }
  }
  return (
    <div className="article-form">
      <h2>Post Gifs</h2>
      <form onSubmit={handleSubmit}>
        <label>Title:</label>
        <br />
        <input
          required
          name="title"
          type="text"
          placeholder="Title"
          onChange={handleChange}
        />
        <br />
        <label>Gifs: </label>
        <br />
        <input type="file" name="imgUrl" onChange={handleImage} />
        <p>
          Tags:{' '}
          {inputs.tags.map(tag => (
            <span key={tag}>{tag}</span>
          ))}
        </p>
        <Tag
          handleTagChange={handleTagChange}
          handleClick={handleClick}
          tag={tag}
        />
        <button type="submit" className="article-btn">
          Post
        </button>
      </form>
    </div>
  );
};

export default CreateGif;
