import React from 'react'

const Gif = (props) => {
  return (
    <div className="article">
      <div className="user-header">
        <img className="prof_img" src={props.user_img} alt="User_Image"/>
        <span className="name-span">{props.name}</span>
        <span className="date-span">{props.date}</span>
      </div>
      <div className="article-body">
        <p className="article-title">{props.gif_title}</p>
      <img className="gif" src={props.gif_Url} alt="gifpost" />
      </div>
    </div>
  )
}

export default Gif
