import React from 'react';

const PostComments = props => {
  return (
    <form onSubmit={props.handleSubmit}>
      <textarea
        value={props.value}
        placeholder="comments"
        onChange={props.handleChange}
      ></textarea>
      <button className="add-comment">Add</button>
    </form>
  );
};

export default PostComments;
